.levels {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.svg-div {
  background-color: rgb(236, 238, 232);
  background-size: cover;
}
.svg-pieces {
  background-color: rgba(238, 237, 232, 0.411);
  background-size: cover;
}

.f3 {
  /* color: #95dbe5ff; */
  color: black;
}
.f6,
.pa3 {
  background-color: white;
}

.highscores {
  color: white;
  font-size: larger;
  /* background-image: url("https://images.app.goo.gl/87JMuyATUGcjJWhs7"); */
  margin: auto;
  margin-top: 20px;

  padding: 25px;
  width: 20%;
  height: 20px;
  border-collapse: collapse;
  /* border: 1px solid red; */
}

.highscores > * {
  color: black;
  font-size: larger;
  text-align: left;
  border-bottom: 1px transparent;
  opacity: 1;
}

.highscores > * > * {
  padding-left: 10px;
}

/* ------ Help CSS ------ */
.help {
  position: absolute;
  top: 8px;
  left: 8px;
  cursor: pointer;
}

#idHelp {
  position: absolute;
  height: 95vh;
  width: 30vw;
  overflow-y: auto;
  color: white;
  font-size: 1.6rem;
  background-color: black;
  text-align: left;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 300ms;
  transition: -webkit-transform 300ms;
  transition: transform 300ms;
  transition: transform 300ms, -webkit-transform 300ms;
}

#idHelp.isopen {
  -webkit-transform: none;
          transform: none;
}

#help {
  position: relative;
  float: left;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

/* ------ Steps CSS ------ */

.buttons-divBP {
  display: grid;
  grid-template-rows: 0.2fr 1fr 1fr 0.1fr;
  height: 75vh;
}

.buttonsBP {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  margin: auto;
}

#idTranslate,
#idRotPlus,
#idRotMin,
#idRefX,
#idRefY {
  height: 7vmin;
  width: 7vmin;
  cursor: pointer;
  border: 1px solid dimgray;
  border-radius: 12px;
  margin: auto;
}

#idTranslate:hover,
#idRotPlus:hover,
#idRotMin:hover,
#idRefX:hover,
#idRefY:hover,
.listButton:hover {
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.steps {
  display: grid;
  grid-template-rows: 0.2fr 1fr 0.2fr;
  width: 80%;
  margin: auto;
}

.stepsList {
  /* grid-template-columns: 1fr 1fr; */
  font-size: 1.5rem;
  text-align: left;
  height: 250px;
  overflow-y: auto;
  border-radius: 12px;
  padding-left: 15px;
  border: double;
}

.divButton {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 20px;
}
.listButton {
  cursor: pointer;
  border: 1px solid dimgray;
  border-radius: 12px;
  color: white;
  font-size: 1.5rem;
  text-align: center;
  font-weight: bold;
}

.clear {
  background-color: red;
  opacity: 0.9;
}
.trans {
  background-color: green;
  opacity: 0.9;
}

